body {
  height: 100%;
}

.align-middle {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.btn {
  &.btn-transparent {
    display: inline-block;
    margin-bottom: 1rem;
    color: rgba(255, 255, 255, 0.7);
    background-color: rgba(255, 255, 255, 0.08);
    border-color: rgba(255, 255, 255, 0.2);
    border-style: solid;
    border-width: 1px;
    border-radius: 0.3rem;
    transition: color 0.2s, background-color 0.2s, border-color 0.2s;
    &:hover {
      color: rgba(255, 255, 255, 0.8);
      text-decoration: none;
      background-color: rgba(255, 255, 255, 0.2);
      border-color: rgba(255, 255, 255, 0.3);
    }
  }
}

.wrapper {
  nav {
    border: none;
    background-image: linear-gradient(120deg, #30809A, #77CEA2);
    a {
      color: #FFF;
    }
    .nav li a {
      &:hover,
      &:focus {
        background-color: rgba(234, 136, 136, 0.78);
        outline: none;
      }
      &.active {
        background-color: #EA8888;
      }
    }
  }
  .page-wrap {
    position: relative;
    top: 7rem;
    .code-pre {
      display: block;
      margin: 0 0 1rem;
      font-size: 1.3rem;
      word-break: break-all;
      word-wrap: break-word;
      color: #333;
      background-color: #f5f5f5;
      border: 1px solid #ccc;
      border-radius: 0.4rem;
    }
  }
  .p-footer {
    position: relative;
    top: 10rem;
    margin-bottom: 1.8rem;
    border-top: 1px solid #eff0f1;
    padding-top: 1.8rem;
    font-weight: bold;
    margin-top: 3rem;
  }
}

//线性渐变动画
.anim-linear-gradient {
  animation: anim-linear-gradient 35s infinite ease-out;
  transition: linear-gradient 10s ease-out;
}

@keyframes anim-linear-gradient {
  0% {
    background-image: linear-gradient(120deg, rgba(63, 200, 245, 0.7), rgba(255, 120, 47, 0.7));
  }
  25% {
    background-image: linear-gradient(120deg, rgba(63, 200, 245, 0.5), rgba(255, 120, 47, 0.5));
  }
  50% {
    background-image: linear-gradient(120deg, rgba(63, 200, 245, 1), rgba(255, 120, 47, 1));
  }
  75% {
    background-image: linear-gradient(120deg, rgba(63, 200, 245, 0.5), rgba(255, 120, 47, 0.5));
  }
  100% {
    background-image: linear-gradient(120deg, rgba(63, 200, 245, 0.7), rgba(255, 120, 47, 0.7));
  }
}